.menu{
  font-weight: bold;
  border: 1px solid #565387;
}
#header{
  width:100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 30px;
  font-weight: 700;
  color: black;
}
#header .brand-name{
  color: aqua;
} 
#header h2{
  color: #565387;
  font-size: 20px;
}
#header .btn-get-started{
  font-weight:500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius:50px;
  transition: 0.5s;
  color: blue;
  border: 2px solid #3498db;
  text-decoration: none ;

}
#header .btn-get-started:hover{
  background: #3498db;
  color: #fff;
}
 #header-img{
   text-align: right;
   
 }
 #header .fluid{
   animation:up-down 2s ease-in-out infinite alternate-reverse both;
 }
 @-webkit-keyframes up-down{
   0%{
     transform: translateY(10px);
   }
   100%{
     transform: translateY(-10px);
   }
 }
@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}

/*end of main page*/